  const calculatePrice = async () => {
    try {
      const fromPlace = fromAddressRef.current.value;
      const toPlace = toAddressRef.current.value;
      if (!fromPlace || !toPlace) return;

      const service = new window.google.maps.DistanceMatrixService();
      service.getDistanceMatrix(
        {
          origins: [fromPlace],
          destinations: [toPlace],
          travelMode: 'DRIVING',
        },
        (response, status) => {
          if (status === 'OK') {
            const distance = response.rows[0].elements[0].distance.value / 1000; // Vzdlenost v kilometrech
            // Zde mete provst vpoet ceny na zklad vzdlenosti
            const startingRate = 40; // Nastupn sazba
            const ratePerKm = 40; // Sazba za kilometr
            let totalPrice = startingRate + distance * ratePerKm;

            // Upraven ceny na zklad promo kdu
            if (promoCode.toUpperCase() === 'VIP10') {
               discountedPrice = totalPrice * 0.95; // Sleva 5 %
               console.log("Po 5% slev je cena: " + discountedPrice)
            } else if (promoCode.toUpperCase() === 'SUPERVIP') {
              discountedPrice = totalPrice * 0.9; // Sleva 10 %
            }

            totalPrice = Math.ceil(totalPrice); // Zaokrouhlen nahoru na cel slo
            formattedPrice = totalPrice.toLocaleString('cs-CZ');
            setPrice(formattedPrice);

            // Podmnka pro zobrazen slevy na zklad promo kdu
            if (promoCode.toUpperCase() === 'VIP10') {
              totalPriceWithDiscount = Math.ceil(discountedPrice);
              console.log("Nov cena po slev je: " + totalPriceWithDiscount)
              setTotalPriceDiscounted(totalPriceWithDiscount.toLocaleString('cs-CZ'));
            } else if (promoCode.toUpperCase() === 'SUPERVIP') {
              totalPriceWithDiscount = Math.ceil(discountedPrice);
              console.log("Nov cena po slev je: " + totalPriceWithDiscount)
              setTotalPriceDiscounted(totalPriceWithDiscount.toLocaleString('cs-CZ'));
            } else {
              setTotalPriceDiscounted(''); // Pokud nen promo kd zadn, vynulujte zobrazenou slevu
              console.log("Promokod neni zadan.")
            }
          } else {
            console.error('Error calculating distance:', status);
          }
        }
      );
    } catch (error) {
      console.error('Error calculating price:', error);
    }
  };

  const handlePromoCodeSubmit = (event) => {
    event.preventDefault(); // Zastaven vchoz akce formule
    // Zde se nastav promo kd na hodnotu z pole
    setPromoCode(promoCodeRef.current.value.toUpperCase()); // Peveden hodnoty na velk psmena
    calculatePrice(); // Pepotn ceny po zmn promo kdu
  };

